*,
*::after,
*::before {
  box-sizing: border-box;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
}
.page {
  --size: 14px;
  --line: 1.3;
  --weight: 400;
  font-family: "Lato", "Arial", sans-serif;
  font-size: var(--size);
  line-height: var(--line);
  color: #000000;
  font-weight: var(--weight);
  width: 100%;
  height: 100%;
  counter-reset: list-item;
}
@media (min-width: 768px) {
  .page {
    --size: 16px;
  }
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

@media (max-width: 767.98px) {
  .main-header--inner {
    border-bottom: solid 1px #d9d9d9;
  }
}
@media (min-width: 768px) {
  .main-header--inner .main-navigation__link[aria-current=step] {
    text-underline-offset: 10px;
  }
}

@media (min-width: 768px) {
  .main-header--form .main-navigation__list {
    column-gap: 34px;
  }
}

.main-header__container {
  margin-inline: 20px;
  padding: 0 0 5px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header__container {
    margin-inline: 30px;
  }
}
@media (min-width: 1440px) {
  .main-header__container {
    inline-size: 1220px;
    margin-inline: auto;
  }
}
@media (min-width: 768px) {
  .main-header__container {
    display: grid;
    grid-template-columns: 174px 1fr;
    column-gap: 90px;
    align-items: start;
  }
}
@media (min-width: 1440px) {
  .main-header__container {
    grid-template-columns: 202px 1fr;
    padding-top: 53px;
  }
}

.main-header__logo {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: var(--size);
  line-height: var(--line);
  text-decoration: none;
  text-transform: uppercase;
  color: inherit;
  justify-self: flex-start;
  width: 210px;
  height: 60px;
}
.main-header__logo[aria-current=page] {
  pointer-events: none;
}
@media (min-width: 768px) {
  .main-header__logo {
    width: 182px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    width: 220px;
  }
}

.main-header__logo-image {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  justify-self: flex-start;
}
.main-header__logo-image:hover {
  opacity: 0.8;
}
.main-header__logo-image:active {
  opacity: 0.6;
}

@media (max-width: 767.98px) {
  .main-header__container--nojs {
    flex-direction: column;
    padding: 0;
  }
  .main-header__container--nojs .main-header__toggle-navigation {
    display: none;
  }
  .main-header__container--nojs .main-navigation {
    position: static;
    display: block;
  }
  .main-header__container--nojs .main-header__logo {
    align-self: flex-start;
  }
}

.main-header__toggle-navigation {
  cursor: pointer;
  position: relative;
  display: none;
}
@media (max-width: 767.98px) {
  .main-header__toggle-navigation {
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 24px;
    height: 16px;
    background-color: #ffffff;
    border: none;
  }
  .main-header__toggle-navigation:hover {
    opacity: 0.6;
  }
  .main-header__toggle-navigation:active {
    opacity: 0.3;
  }
}

.main-navigation {
  text-align: center;
}
@media (max-width: 767.98px) {
  .main-navigation {
    display: none;
  }
  .main-navigation + .main-header__toggle-navigation::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
}
@media (max-width: 767.98px) {
  .main-navigation--opened {
    display: block;
    position: absolute;
    width: 100%;
    min-height: 65px;
    background-color: #ffffff;
    top: 65px;
    left: 0;
    z-index: 2;
  }
  .main-navigation--opened + .main-header__toggle-navigation {
    background-color: #ffffff;
  }
  .main-navigation--opened + .main-header__toggle-navigation::before, .main-navigation--opened + .main-header__toggle-navigation::after {
    content: "";
    right: 0;
    top: 50%;
    position: absolute;
    width: 25px;
    height: 2px;
    background-color: #000000;
  }
  .main-navigation--opened + .main-header__toggle-navigation::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-navigation--opened + .main-header__toggle-navigation::after {
    transform: rotate(-45deg);
  }
  .main-navigation--opened .main-navigation__list:first-child {
    border-top: solid 1px #e6e6e6;
  }
}

.main-navigation__list {
  padding: 0;
  list-style: none;
  margin: 0;
}
@media (min-width: 768px) {
  .main-navigation__list {
    position: relative;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    column-gap: 36px;
    background-color: transparent;
  }
}
@media (min-width: 1440px) {
  .main-navigation__list {
    column-gap: 35px;
  }
}

@media (min-width: 1440px) {
  .main-navigation__list--main .main-navigation__link {
    color: #ffffff;
    background-color: transparent;
  }
  .main-navigation__list--main .main-navigation__link[aria-current=step] {
    text-decoration-color: #ffffff;
  }
}

.main-navigation__link {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: var(--size);
  line-height: var(--line);
  text-decoration: none;
  text-transform: uppercase;
  color: inherit;
  padding: 22px 10px 23px 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  border-bottom: solid 1px #e6e6e6;
}
.main-navigation__link:hover {
  opacity: 0.6;
}
.main-navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-navigation__link {
    padding: 19px 0 23px 0;
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.3;
    border: none;
  }
}
@media (min-width: 1440px) {
