    font-size: 20px;
  }
}
.main-navigation__link[aria-current=step] {
  pointer-events: none;
}
@media (min-width: 768px) {
  .main-navigation__link[aria-current=step] {
    text-decoration: underline 2px #68b738;
    text-underline-offset: 12px;
  }
}
@media (min-width: 1440px) {
  .main-navigation__link[aria-current=step] {
    text-underline-offset: 11px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.intro {
  font-family: "Oswald", "Arial", sans-serif;
