}
.additional-card__button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .additional-card__button {
    padding: 10px;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .additional-card__button {
    grid-column: -2/-1;
    padding: 10px 26px 9px 26px;
  }
}

.additional-card__units {
  margin: 0;
}
@media (min-width: 768px) {
  .additional-card__units {
    justify-self: end;
  }
}
@media (min-width: 1440px) {
  .additional-card__units {
    justify-self: start;
  }
}

.additional-card__price {
  margin: 0;
  justify-self: end;
}
@media (min-width: 1440px) {
  .additional-card__price {
    justify-self: start;
  }
}

.special-offer {
  position: relative;
  background-image: url("../img/cat-catalog-bag-mobile@1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #9b9b9b;
  width: auto;
  height: 290px;
}
.special-offer::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 290px;
  background-color: #68b738;
  opacity: 0.85;
  top: 0;
  left: 0;
}
.special-offer::after {
  position: absolute;
  content: "";
  width: 82px;
  height: 82px;
  top: 59px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/stack.svg#gift");
}
@media (min-width: 768px) and (max-width: 1439.98px) {
  .special-offer::after {
    top: 49%;
    left: 77px;
    transform: translateY(-50%);
  }
}
@media (min-width: 1440px) {
  .special-offer::after {
    top: 56px;
    left: 51%;
  }
}
@media (min-width: 768px) {
  .special-offer {
    background-image: url("../img/cat-catalog-bag-tablet@1.jpg"), linear-gradient(to right, #1b1b1b, #fcfcfc);
    width: 100%;
    height: 200px;
  }
  .special-offer::before {
    height: 200px;
  }
}
@media (min-width: 1440px) {
  .special-offer {
    grid-column: 2/3;
    background-image: url("../img/cat-catalog-bag-desktop@1.jpg");
    height: 289px;
  }
  .special-offer::before {
    max-width: 250px;
    height: 289px;
  }
}

.special-offer__text {
  margin: 0;
  padding: 186px 61px 43px 58px;
  position: relative;
  font-size: 16px;
  line-height: 1.25;
  color: #ffffff;
  z-index: 1;
  text-align: center;
}
@media (min-width: 768px) and (max-width: 1439.98px) {
  .special-offer__text {
    padding: 71px 124px 69px 422px;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .special-offer__text {
    padding: 185px 43px 42px 40px;
  }
}